const StockTable = ({ stocks }) => {
    if (stocks.length === 0) {
        return <p className="error-message">  .</p>;
    }

    const years = ["2021", "2022", "2023", "2024"];
    const financialKeys = [
        "",
        "",
        "",
        "",
        "EPS",
        "PER",
        "PBR",
        "",
        "",
    ];

    const unitMap = {
        "EPS": "()",
        "PER": "()",
        "PBR": "()",
        "": "()",
        "": "(%)",
        "": "(%)",
        "": "(%)",
    };

    const cleanValue = (value, key) => {
        if (!value || value === "-" || value === "N/A" || value === "") return "N/A";

        const numericValue = parseFloat(value.toString().replace(/,/g, ""));

        //       
        if (key === "" || key === "") {
            const trillionPart = Math.floor(numericValue / 10000); //  
            const billionPart = numericValue % 10000; //  

            if (trillionPart > 0) {
                return `${trillionPart} ${billionPart}`;
            }
            return `${billionPart}`; //    
        }

        //  (%)
        if (key === "" || key === "" || key === "") {
            return `${numericValue}%`;
        }

        //  
        if (key === "EPS" || key === "") {
            return `${numericValue}`;
        }

        //  
        if (key === "PER" || key === "PBR") {
            return `${numericValue}`;
        }

        return value.toString().replace(/,/g, ""); // :    
    };

    const transformedData = financialKeys.map((key) => {
        const rowData = { key };

        years.forEach((year) => {
            const columnNameWithUnit = `${year}.12 ${key}${unitMap[key] || ""}`;
            const columnNameWithoutUnit = `${year}.12 ${key}`;
            const value =
                columnNameWithUnit in stocks[0]
                    ? stocks[0][columnNameWithUnit]
                    : columnNameWithoutUnit in stocks[0]
                    ? stocks[0][columnNameWithoutUnit]
                    : "N/A";

            rowData[year] = cleanValue(value, key);
        });

        return rowData;
    });

    console.log("Transformed Data for Row-Based Table:", transformedData);

    return (
        <div className="table-container">
            <table>
                <thead>
                    <tr>
                        <th></th>
                        {years.map((year, index) => (
                            <th key={index}>{year}</th>
                        ))}
                    </tr>
                </thead>
                <tbody>
                    {transformedData.map((row, index) => (
                        <tr key={index}>
                            <td>{row.key}</td>
                            {years.map((year, idx) => (
                                <td key={idx}>{row[year]}</td>
                            ))}
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

export default StockTable;
