import React, { useState, useEffect } from "react";
import axios from "axios";
import SimpleTable from "./SimpleTable";
import "../styles/Common.css";
import "../styles/FinancialStatementsPage.css";



const FinancialStatementsPage = () => {
    const [stocks, setStocks] = useState([]);
    const [error, setError] = useState("");
    const [selectedYear, setSelectedYear] = useState(null);
    const [currentMetric, setCurrentMetric] = useState(null);
    const [showYearButtons, setShowYearButtons] = useState(false); //    
    const [perDirection, setPerDirection] = useState("top"); // PER  
    const [pbrDirection, setPbrDirection] = useState("top"); // PBR  

    //  
    const fetchTopStocksByMetric = () => {
        if (currentMetric === "") {
            axios
                .get("https://port-0-stockter-back-m5or7nt39f4a0f5c.sel4.cloudtype.app/top-marketcap")
                .then((response) => {
                    if (response.data.error || response.data.stocks.length === 0) {
                        setError("  .");
                        setStocks([]);
                    } else {
                        setStocks(response.data.stocks);
                        setError("");
                    }
                })
                .catch((error) => {
                    console.error("    :", error);
                    setError("   .");
                    setStocks([]);
                });
        } else if (currentMetric === "") {
            axios
                .get("https://port-0-stockter-back-m5or7nt39f4a0f5c.sel4.cloudtype.app/top-revenue", { params: { year: selectedYear } })
                .then((response) => {
                    if (response.data.error || response.data.stocks.length === 0) {
                        setError("  .");
                        setStocks([]);
                    } else {
                        setStocks(response.data.stocks);
                        setError("");
                    }
                })
                .catch((error) => {
                    console.error("    :", error);
                    setError("   .");
                    setStocks([]);
                });
        } else if (currentMetric === "") {
            axios
                .get("https://port-0-stockter-back-m5or7nt39f4a0f5c.sel4.cloudtype.app/top-operating-income", { params: { year: selectedYear } })
                .then((response) => {
                    if (response.data.error || response.data.stocks.length === 0) {
                        setError("  .");
                        setStocks([]);
                    } else {
                        setStocks(response.data.stocks);
                        setError("");
                    }
                })
                .catch((error) => {
                    console.error("    :", error);
                    setError("   .");
                    setStocks([]);
                });
        } else if (currentMetric === "") {
            axios
                .get("https://port-0-stockter-back-m5or7nt39f4a0f5c.sel4.cloudtype.app/top-operating-income-rate", { params: { year: selectedYear } })
                .then((response) => {
                    if (response.data.error || response.data.stocks.length === 0) {
                        setError("  .");
                        setStocks([]);
                    } else {
                        setStocks(response.data.stocks);
                        setError("");
                    }
                })
                .catch((error) => {
                    console.error("    :", error);
                    setError("   .");
                    setStocks([]);
                });
        } else if (currentMetric === "") {
            axios
                .get("https://port-0-stockter-back-m5or7nt39f4a0f5c.sel4.cloudtype.app/top-net-income", { params: { year: selectedYear } })
                .then((response) => {
                    if (response.data.error || response.data.stocks.length === 0) {
                        setError("  .");
                        setStocks([]);
                    } else {
                        setStocks(response.data.stocks);
                        setError("");
                    }
                })
                .catch((error) => {
                    console.error("    :", error);
                    setError("   .");
                    setStocks([]);
                });
        } else if (currentMetric === "EPS") {
            axios
                .get("https://port-0-stockter-back-m5or7nt39f4a0f5c.sel4.cloudtype.app/top-eps", { params: { year: selectedYear } })
                .then((response) => {
                    if (response.data.error || response.data.stocks.length === 0) {
                        setError("EPS  .");
                        setStocks([]);
                    } else {
                        setStocks(response.data.stocks);
                        setError("");
                    }
                })
                .catch((error) => {
                    console.error("    :", error);
                    setError("   .");
                    setStocks([]);
                });
        } else if (currentMetric === "PER") {
            const endpoint = perDirection === "top" ? "top-per" : "bottom-per";
            axios
                .get(`https://port-0-stockter-back-m5or7nt39f4a0f5c.sel4.cloudtype.app/${endpoint}`, { params: { year: selectedYear } })
                .then((response) => {
                    if (response.data.error || response.data.stocks.length === 0) {
                        setError("PER  .");
                        setStocks([]);
                    } else {
                        setStocks(response.data.stocks);
                        setError("");
                    }
                })
                .catch((error) => {
                    console.error("    :", error);
                    setError("   .");
                    setStocks([]);
                });
        } else if (currentMetric === "PBR") { // PBR  
            const endpoint = pbrDirection === "top" ? "top-pbr" : "bottom-pbr";
            axios
                .get(`https://port-0-stockter-back-m5or7nt39f4a0f5c.sel4.cloudtype.app/${endpoint}`, { params: { year: selectedYear } })
                .then((response) => {
                    if (response.data.error || response.data.stocks.length === 0) {
                        setError("PBR  .");
                        setStocks([]);
                    } else {
                        setStocks(response.data.stocks);
                        setError("");
                    }
                })
                .catch((error) => {
                    console.error("    :", error);
                    setError("   .");
                    setStocks([]);
                });
        } else if (currentMetric === "") { //   
            axios
                .get("https://port-0-stockter-back-m5or7nt39f4a0f5c.sel4.cloudtype.app/top-dividend-yield", { params: { year: selectedYear } }) //   
                .then((response) => {
                    if (response.data.error || response.data.stocks.length === 0) {
                        setError("  .");
                        setStocks([]);
                    } else {
                        setStocks(response.data.stocks);
                        setError("");
                    }
                })
                .catch((error) => {
                    console.error("    :", error);
                    setError("   .");
                    setStocks([]);
                });
        } else {
            setError(`"${currentMetric}"    .`);
            setStocks([]);
        }
    };

    //   
    const handleButtonClick = (metric) => {
        if (metric === "PER") {
            if (currentMetric === "PER") {
                // PER  
                setPerDirection((prevDirection) => (prevDirection === "top" ? "bottom" : "top"));
            } else {
                setPerDirection("top"); //   
            }
        } else if (metric === "PBR") {
            if (currentMetric === "PBR") {
                // PBR  
                setPbrDirection((prevDirection) => (prevDirection === "top" ? "bottom" : "top"));
            } else {
                setPbrDirection("top"); //   
            }
        }

        setCurrentMetric(metric);
        if (metric !== "") {
            setShowYearButtons(true); //         
            setSelectedYear("2024"); //   
        } else {
            setShowYearButtons(false); //      
            setSelectedYear(null);
        }
    };

    //    
    const handleYearButtonClick = (year) => {
        setSelectedYear(year);
    };

    //   
    useEffect(() => {
        if (currentMetric) {
            fetchTopStocksByMetric();
        }
    }, [selectedYear, currentMetric, perDirection, pbrDirection]); // pbrDirection 

    return (
        <div className="financial-statements-container">
            <h2 style={{ color: "#007bff", marginBottom: "20px" }}></h2>

            {/*   */}
            <div style={{ display: "flex", alignItems: "center", gap: "10px", marginBottom: "10px" }}>
                {/*   */}
                <button
                    onClick={() => handleButtonClick("")}
                    style={{
                        backgroundColor: currentMetric === "" ? "#007bff" : "#fff",
                        color: currentMetric === "" ? "#fff" : "#000",
                        border: "1px solid #007bff",
                        borderRadius: "4px",
                        padding: "5px 10px",
                    }}
                >
                    
                </button>

                {/*   */}
                <button
                    onClick={() => handleButtonClick("")}
                    style={{
                        backgroundColor: currentMetric === "" ? "#007bff" : "#fff",
                        color: currentMetric === "" ? "#fff" : "#000",
                        border: "1px solid #007bff",
                        borderRadius: "4px",
                        padding: "5px 10px",
                    }}
                >
                    
                </button>

                {/*   */}
                <button
                    onClick={() => handleButtonClick("")}
                    style={{
                        backgroundColor: currentMetric === "" ? "#007bff" : "#fff",
                        color: currentMetric === "" ? "#fff" : "#000",
                        border: "1px solid #007bff",
                        borderRadius: "4px",
                        padding: "5px 10px",
                    }}
                >
                    
                </button>

                {/*   */}
                <button
                    onClick={() => handleButtonClick("")}
                    style={{
                        backgroundColor: currentMetric === "" ? "#007bff" : "#fff",
                        color: currentMetric === "" ? "#fff" : "#000",
                        border: "1px solid #007bff",
                        borderRadius: "4px",
                        padding: "5px 10px",
                    }}
                >
                    
                </button>

                {/*   */}
                <button
                    onClick={() => handleButtonClick("")}
                    style={{
                        backgroundColor: currentMetric === "" ? "#007bff" : "#fff",
                        color: currentMetric === "" ? "#fff" : "#000",
                        border: "1px solid #007bff",
                        borderRadius: "4px",
                        padding: "5px 10px",
                    }}
                >
                    
                </button>

                {/* EPS  */}
                <button
                    onClick={() => handleButtonClick("EPS")}
                    style={{
                        backgroundColor: currentMetric === "EPS" ? "#007bff" : "#fff",
                        color: currentMetric === "EPS" ? "#fff" : "#000",
                        border: "1px solid #007bff",
                        borderRadius: "4px",
                        padding: "5px 10px",
                    }}
                >
                    EPS
                </button>

                {/* PER  */}
                <button
                    onClick={() => handleButtonClick("PER")}
                    style={{
                        backgroundColor: currentMetric === "PER" ? "#007bff" : "#fff",
                        color: currentMetric === "PER" ? "#fff" : "#000",
                        border: "1px solid #007bff",
                        borderRadius: "4px",
                        padding: "5px 10px",
                    }}
                >
                    PER {currentMetric === "PER" ? (perDirection === "top" ? "" : "") : ""}
                </button>

                {/* PBR  */}
                <button
                    onClick={() => handleButtonClick("PBR")}
                    style={{
                        backgroundColor: currentMetric === "PBR" ? "#007bff" : "#fff",
                        color: currentMetric === "PBR" ? "#fff" : "#000",
                        border: "1px solid #007bff",
                        borderRadius: "4px",
                        padding: "5px 10px",
                    }}
                >
                    PBR {currentMetric === "PBR" ? (pbrDirection === "top" ? "" : "") : ""}
                </button>

                {/*   */}
                <button
                    onClick={() => handleButtonClick("")}
                    style={{
                        backgroundColor: currentMetric === "" ? "#007bff" : "#fff",
                        color: currentMetric === "" ? "#fff" : "#000",
                        border: "1px solid #007bff",
                        borderRadius: "4px",
                        padding: "5px 10px",
                    }}
                >
                    
                </button>
            </div>

            {/*   (      ) */}
            {showYearButtons && (
                <div style={{ display: "flex", gap: "5px", marginBottom: "20px" }}>
                    {["2021", "2022", "2023", "2024"].map((year) => (
                        <button
                            key={year}
                            onClick={() => handleYearButtonClick(year)}
                            style={{
                                backgroundColor: selectedYear === year ? "#007bff" : "#fff",
                                color: selectedYear === year ? "#fff" : "#000",
                                border: "1px solid #007bff",
                                borderRadius: "4px",
                                padding: "5px 10px",
                            }}
                        >
                            {year}
                        </button>
                    ))}
                </div>
            )}

            {error && <p className="error-message">{error}</p>}

            {/*   */}
            {stocks.length > 0 && (
                <SimpleTable
                    stocks={stocks}
                    columnTitle={currentMetric} //   
                />
            )}
        </div>
    );

};

export default FinancialStatementsPage;
